/*google font*/

::selection {
    background: #00b0ff;
    color: #fff;
    text-shadow: none;
}

.bg-theme {
    background-color: #900C3F;
}

.bg-theme-white {
    background-color: #fff;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

*:focus {
    outline: 0;
}

html {
    font-size: 62.5%;
    -webkit-font-smoothing: antialiased;
}

body {
    background: #fff;
    font-weight: normal;
    font-size: 15px;
    letter-spacing: 1.5px;
    color: #888;
    line-height: 30px;
    -webkit-font-smoothing: antialiased;
    /* Fix for webkit rendering */
    -webkit-text-size-adjust: 100%;
}

img {
    max-width: 100%;
    height: auto;
}

button,
input,
textarea {
    letter-spacing: 1px;
}

/*===========================
    2.1 Header Area
===========================*/

.top-img {
    position: relative;
}

.mobile-menu {
    list-style: none;
    display: flex;
    flex-flow: row nowrap;
}
.mobile-menu li {
    padding: 18px 10px;
}

.header-area {
    position: fixed;
    padding: 0 5px 0 5px;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 9999;
    transition: all 0.3s ease 0s;
}

.header-area.header_fix {
    background: #900C3F;
    z-index: 9;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
}

.menu-area {
    display: flex;
}

.logo {
    margin-top: 20px;
}

.logo a {
    display: inline-block;
    text-decoration: none;
}

.logo a p {
    font-family: 'AdineKirnbergRegular', sans-serif;
    font-size: 60px;
    color: #fff;
    margin: 0;
    text-indent: 0;
    min-width: 300px;
}

.main-menu {
    text-align: right;
}

.main-menu nav ul {}

.main-menu nav ul li {
    display: inline-block;
}

.main-menu nav ul li a {
    font-size: 14px;
    font-weight: 500;
    color: #fff;
    display: block;
    padding: 33px 12px;
    letter-spacing: 0;
    text-transform: capitalize;
    text-decoration: none;
}

.main-menu nav ul li a:hover,
.main-menu nav ul li.active a {
    color: rgb(194, 183, 183);
}

@media (max-width:767px){.hidden-xs{display:none!important}}
@media (min-width:768px) and (max-width:995px){.hidden-sm{display:none!important}}
@media (min-width:768px) and (max-width:995px){.visible-sm{display:block!important}}
@media (max-width:767px){.visible-xs{display:block!important}}

/*===========================
  END Header Area
  ===========================*/


