/*===========================
2.7 Pricing Area
===========================*/

.responsive-table {
    padding: 0;
}
.wrapper {
    /* margin-top: -100%; */
    display: none;
}
.wrapper.is-table-open {
    /* margin-top: 0; */
    display: block;
}
.responsive-table li {
    border-radius: 3px;
    padding: 15px 30px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
}
.responsive-table .table-header {
    position: relative;
    background-color: #900C3F;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 0.03em;
}
.table-open {
    position: absolute;
    right: 25px;
}
.table-open_btn {
    color: #fff;
    text-decoration: none;
}
.responsive-table .table-header .col-1 {
    display: none;
}
.responsive-table .table-row {
    font-size: 19px;
    background-color: #ffffff;
    box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.1);
}
.responsive-table .col-0 {
    flex-basis: 5%;
}
.responsive-table .col-1 {
    flex-basis: 10%;
}
.responsive-table .col-2 {
    flex-basis: 40%;
}
.responsive-table .col-3 {
    flex-basis: 25%;
}
.responsive-table .col-4 {
    flex-basis: 25%;
}
.mobile-title {
    display: none;
}

.table-header .col {
    color: #fff;
}

@media all and (max-width: 767px) {
    .mobile-title {
        width: 65px;
        height: 50px;
        margin-right: 10px;
        display: inline-block;
    }
    .responsive-table .table-row {
        font-size: 14px;
    }
    .responsive-table .table-header {
        padding: 15px 20px;
    }
    .table-open {
        position: absolute;
        right: 20px;
        top: 20px;
    }
    .table-open_btn {
        color: #fff;
        text-decoration: none;
    }
    .responsive-table .table-header .col-1 {
        color: #fff;
        display: flex;
    }
    .responsive-table .table-header .col-2 {
        display: none;
    }
    .responsive-table .table-header .col-3 {
        display: none;
    }
    .responsive-table .table-row {
        display: flex;
        flex-direction: column;
    }
    .table-row{}
    .responsive-table li {
        display: block;
    }
    .responsive-table .col {
        width: 100%;
        flex-basis: 100%;
        display: flex;
        padding: 10px 0;
    }
    .table-row .col-3 {
        display: inline-block;
        height: 10px;
    }
}

