.h-divider {
    margin: auto;
    margin-top: 80px;
    margin-bottom: 30px;
    width: 80%;
    position: relative;
}

.h-divider .shadows {
    overflow: hidden;
    height: 20px;
}

.h-divider .shadows:after {
    content: '';
    display: block;
    margin: -25px auto 0;
    width: 100%;
    height: 25px;
    border-radius: 125px/12px;
    box-shadow: 0 0 8px #900C3F;
}

.h-divider .text {
    width: 100px;
    height: 45px;
    padding: 10px;
    position: absolute;
    bottom: 100%;
    margin-bottom: -33px;
    left: 50%;
    margin-left: -60px;
    border-radius: 100%;
    box-shadow: 0 2px 4px #900C3F;
    background: white;
}

.h-divider .text i {
    position: absolute;
    top: 4px;
    bottom: 4px;
    left: 4px;
    right: 4px;
    border-radius: 100%;
    border: 1px dashed #900C3F;
    text-align: center;
    line-height: 50px;
    font-style: normal;
    color: #900C3F;
}

.h-divider .text2 {
    width: 70px;
    height: 70px;
    position: absolute;
    bottom: 100%;
    margin-bottom: -35px;
    left: 50%;
    margin-left: -25px;
    border-radius: 100%;
    box-shadow: 0 2px 4px #900C3F;
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

