div#preloader {
  height: 100%;
  left: 0;
  overflow: visible;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
  display: flex;
  background: #fff;
}


.spinner {
  width: 40px;
  height: 40px;
  margin: auto;
  background-color: #333;

  border-radius: 100%;
  animation: sk-scaleout 1.0s infinite ease-in-out;
}

@keyframes sk-scaleout {
  0% {
      transform: scale(0);
  }
  100% {
      transform: scale(1.0);
      opacity: 0;
  }
}


